.order-page {
  &__inner {
    @include flex(flex-start, normal, row, wrap); }
  &__left {
    width: percentage(415 / 1140);
    padding-right: rem(5);
    @include md {
      width: 100%;
      padding-right: 0; } }
  &__right {
    width: percentage((1140 - 415) / 1140);
    padding-left: rem(5);
    @include md {
      width: 100%;
      padding-left: 0; } } }
