.form {
  &__title {
    text-transform: uppercase;
    font-size: em(30);
    font-weight: 500;
    margin-bottom: rem(15); }
  &__field {
    margin-bottom: rem(5); }
  &__block {
    @include notlast {
      margin-bottom: rem(20); } }
  &__block-title {
    text-transform: none;
    margin-bottom: rem(10); }
  &__row {
    margin-left: rem(-5);
    margin-right: rem(-5);
    @include flex(flex-start, normal, row, wrap);
    margin-bottom: rem(10); }
  &__col {
    width: 50%;
    padding-left: rem(5);
    padding-right: rem(5);
    margin-bottom: rem(10);
    @include xs {
      width: 100%; }
    &--full {
      width: 100%; } }
  &__btn {
    text-align: center; } }
