.header {
  $this: &;
  &__top {
    border-bottom: 1px solid #e6e6e6; }
  &__inner {
    @include flex(flex-start, center, row, wrap);
    padding-top: rem(10);
    padding-bottom: rem(10);
    #{$this}__top & {
      height: $header-top-height;
      @include lg {
        height: $header-top-height-lg; } }
    #{$this}__bottom & {
      height: $header-bottom-height;
      @include lg {
        height: auto;
        padding-top: 0;
        padding-bottom: 0; } } }
  &__bottom {
    background-color: $white;
    border-bottom: 1px solid #e6e6e6;
    @include lg {
      background-color: transparent;
      overflow: hidden; } }
  &__logo {
    margin-right: rem(70);
    @include lg {
      display: none; } }
  &__logo-mob {
    @include lgmin {
      display: none; } }
  &__logo-mob-menu {
    margin-bottom: rem(30);
    @include lgmin {
      display: none; } }
  &__search {
    margin-right: rem(20);
    @include lg {
      margin-right: 0;
      width: calc(50% - #{rem(8)});
      margin-left: rem(4);
      margin-right: rem(4);
      position: relative;
      z-index: 1; } }
  &__btn {
    @include lg {
      width: calc(50% - #{rem(8)});
      margin-left: rem(4);
      margin-right: rem(4);
      position: relative;
      z-index: 1; }
    @include xs {
      width: calc(50% - #{rem(8)}) !important; } }
  &__controls {
    @include flex(flex-start, center);
    @include lg {
      width: calc(100% + #{rem(8)});
      height: rem(90);
      margin-left: rem(-4);
      margin-right: rem(-4);
      background-color: $white;
      position: relative;
      @include before {
        z-index: 0;
        position: absolute;
        left: -40px;
        right: -40px;
        top: 0;
        bottom: 0;
        background-color: $white; } } }
  &__contacts-wrap {
    @include flex(flex-start, center);
    flex-grow: 1;
    @include lg {
      padding-top: rem(20);
      padding-bottom: rem(20); } }
  &__cart {
    margin-left: rem(40);
    @include lg {
      margin-left: rem(20); }
    @include r(374) {
      margin-left: rem(5); } }
  &__contacts {
    flex-grow: 1;
    padding-left: rem(35);
    @include lg {
      padding-left: 0; } }
  &__contacts-mob {
    border-top: 1px solid  $c-border;
    border-bottom: 1px solid  $c-border;
    padding: rem(15) rem(20);
    margin-left: rem(-20);
    margin-right: rem(-20);
    @include lgmin {
      display: none; }
    #{$this}__contact {
      @include notlast {
        margin-bottom: rem(10); } } }
  &__nav-wrap {
    @include lg {
      @include shadow;
      position: absolute;
      top: 0;
      left: 0;
      z-index: 10;
      background-color: $white;
      border-bottom-right-radius: $radius-sm;
      padding: rem(15) rem(20) rem(70);
      width: 460px;
      max-width: percentage(460 / 500);
      transform: translate(-105%, 0);
      @include ready {
        @include tr(transform .5s); }
      &--open {
        transform: translate(0, 0); } } }

  &__nav {
    @include lg {
      padding-top: rem(20); } }
  &__burger {
    color: $accent;
    margin-right: rem(30);
    @include lgmin {
      display: none; }
    @include xxs {
      margin-right: rem(15); }
    @include r(374) {
      margin-right: rem(5); } } }


.header-cart {
  position: relative;
  color: #9e9e9e;
  padding-top: 1em;
  padding-right: 1em;
  .icon {
    font-size: em(33); }
  &__sup {
    @include flex(center, center);
    position: absolute;
    z-index: 1;
    top: rem(-11);
    right: rem(-11);
    background-color: $accent-aux;
    color: $white;
    border-radius: 50%;
    height: rem(22);
    min-width: rem(22);
    @include lg {
      top: 0;
      right: 0;
      height: rem(16);
      min-width: rem(16);
      font-size: .7em; } } }

.burger {
  $this: &;
  display: flex;
  align-items: center;
  justify-content: center;
  width: rem(23);
  height: rem(19);
  position: relative;
  span {
    display: block;
    &, &::after, &::before {
      width: 100%;
      height: rem(2);
      background-color: currentColor;
      backface-visibility: hidden;
      @include tr(transform .3s); }
    &::after, &::before {
      content: "";
      position: absolute;
      left: 0; }
    &::after {
      top: 0; }
    &::before {
      bottom: 0; } }
  // &--active
  //   span
  //     background-color: transparent
  //     &::before
  //       transform: rotate(45deg) translate(-7px,-5px)
  //     &::after
  //       transform: rotate(-45deg) translate(-6px,4px)
  &--lg {
    width: rem(50);
    height: rem(45);
    @include r(374) {
      width: rem(40);
      height: rem(35); }
    span {
      &, &::after, &::before {
        height: rem(4);
        border-radius: $radius; } } } }
