.aside {
  &__block {
    margin-bottom: rem(13);
    @include md {
      height: 100%;
      @include flex(flex-start, normal, column);
      .meta-block__content {
        flex-grow: 1; } }
    &--sliders {
      .meta-block__input:not(.input) {
        @include xs {
          display: inline-block;
          margin-right: rem(10); } } } } }

.aside-filters-block {
  $this: &;
  &__top {
    @include md {
      @include after {
        @include icon-arrow(.8, .8, .1, currentColor, -45, em);
        display: block;
        margin-top: -.2em; } } }
  &__content {
    &:not(#{$this}__content--open) {
      @include hidden;
      overflow: hidden;
      max-height: 0;
      padding: 0; } } }

.aside-filters {
  $this: &;
  &__item {
    position: relative;
    padding-left: rem(35);
    padding-top: rem(8);
    padding-bottom: rem(8);
    @include notlast {
      border-bottom: 1px solid $c-border; } }
  &__link {
    @include hover {
      text-decoration: underline; } }
  &__btn {
    width: 100%;
    display: block;
    text-align: left;
    &--active {
      #{$this}__icon {
        @include after {
          display: none; } } } }
  &__icon {
    font-size: em(20);
    width: 1em;
    height: 1em;
    background-color: $accent;
    border-radius: $radius-sm;
    color: $white;
    position: relative;
    color: $accent-dark;
    margin-right: .5em;
    display: block;
    position: absolute;
    left: 0;
    top: rem(8);
    &::before, &::after {
      content: '';
      background-color: currentColor;
      @include vcenter; }
    &::before {
      width: .7em;
      height: 1px;
      z-index: 1; }
    &::after {
      width: 1px;
      height: .7em;
      z-index: 2; } }
  &__sublist {
    font-size: rem(12);
    font-weight: 600;
    color: $accent;
    // display: none
    @include hidden;
    max-height: 0;
    overflow: hidden;
    &--open {
      @include visible;
      max-height: none;
      overflow: visible; }
    &--second {
      color: $accent-dark;
      padding-left: em(10); } }
  &__subitem {
    position: relative;
    padding-left: em(30);
    margin-left: em(-30);
    padding-top: .7em;
    padding-bottom: .7em; } }



