.banner-slide {
  background-repeat: no-repeat;
  background-position: 100% 0%;
  background-size: auto;
  @include lg {
    background-size: rem(300); }
  @include xs {
    background-size: rem(200); }
  &__title {
    margin-bottom: rem(90);
    @include lg {
      margin-bottom: rem(70); }
    @include xs {
      margin-bottom: rem(30); } } }
