html {
  font-size: $fz;
  line-height: 1.4;
  color: $c-text;
  @include reg;
  // +sm
  //   font-size: $fz-sm-px
  @include xxs {
    font-size: 12px; } }

body {
  background-color: $light;
  min-width: 320px;
  @include no-scroll; }

.icon {
  display: inline-block;
  fill: currentColor;
  font-size: inherit; }

.lazy {
  $this: &;
  &--loaded:not(#{$this}--no-fade-in) {
    animation: fadeIn 1s; } }

.logo {
  display: inline-block;
  font-size: 0; }

.main {
  padding-top: rem(10);
  // padding-bottom: rem(10)
  &__breadcrumbs {
    margin-bottom: rem(10); } }

.container {
  @include lg {
    max-width: none; } }

.search {
  @include flex();
  border-radius:  $radius-sm;
  overflow: hidden;
  &__input, &__btn {
    height: rem(36);
    @include lg {
      height: rem(42); } }
  &__input {
    background-color: $light;
    font-size: em(9.24);
    font-weight: 300;
    padding: 0.1em 1em;
    width: 185px;
    @include lg {
      width: calc(100% - #{rem(42)}); } }
  &__btn {
    @include flex(center, center);
    color: $white;
    background-color: $accent;

    width: rem(36);
    height: rem(36);
    // flex: 0 0 rem(40)
    @include lg {
      width: rem(42); }
    .icon {
      font-size: rem(22); } } }


.scedule {
  @include clr;
  &__title, &__text {
    margin-bottom: .7rem;
    @include xs {
      margin-bottom: .3em; } }
  &__title {
    float: left;
    color: $accent;
    margin-right: .5em; } }

.scroll-top {
  @include flex(center, center);
  font-size: rem(45);
  height: 1em;
  width: 1em;
  border-radius: 50%;
  background-color: $accent;
  color: $white;
  @include tr(opacity .3s);
  @include before {
    @include icon-arrow(.4, .4, .04, currentColor, 135, em);
    display: block;
    margin-top: .15em; }
  @include hover {
    opacity: .7; } }

.block {
  background-color: $white;
  @include shadow;
  border-radius: $radius;
  padding: rem(8);
  &--lg {
    padding: rem(20) rem(20) rem(8); } }

.meta-block {
  @include shadow;
  border-radius: $radius;
  overflow: hidden;
  background-color: $accent;
  &__top {
    // background-color: $accent
    color: $white;
    font-size: em(16);
    padding: .5rem 1rem;
    display: block;
    width: 100%;
    &--has-icon {
      @include flex(flex-start, center); } }
  &__top-icon {
    @extend .burger;
    @include md {
      display: none; } }
  &__title {
    text-align: center;
    flex-grow: 1; }
  &__content {
    background-color: $white;
    border-radius: $radius;
    padding: rem(8) rem(15);
    @include xs {
      padding-left: rem(10);
      padding-right: rem(10); } }
  &__input {
    margin-bottom: rem(10); } }


.rating {
  color: #f5b800;
  @include flex(flex-start, center);
  .icon {
    font-size: em(17);
    @include notlast {
      margin-right: .3em; } } }

.list {
  font-size: em(12);
  li {
    @include list-bullet;
    position: relative;
    padding-left: em(15);
    @include notlast {
      margin-bottom: rem(10); }
    &::before {
      position: absolute;
      top: 0;
      left: 0;
      color: $accent;
      font-size: 1.5em;
      line-height: .9; } } }

.close {
  color: #9f9f9f;
  @include icon-close(#{em(17)}, .2em, currentColor);
  display: block;
  @include tr(color .3s);
  @include hover {
    color: $accent; } }

