.cart {
  $this: &;
  &__item {
    @include notlast {
      margin-bottom: rem(10); } }
  &__bottom {
    @include flex(space-between, center);
    padding-bottom: rem(30);
    padding-top: rem(40);
    @include xs {
      display: block; } }
  &__price {
    flex-grow: 1;
    @include xs {
      margin-bottom: rem(30); } }
  &__amount {
    @include flex(space-between, center);
    padding-right: rem(20);
    padding-bottom: rem(20); }
  &__controls-title {
    font-size: em(16);
    font-weight: 500; }
  &__controls {}

  &--order {
    padding-left: rem(20);
    padding-right: rem(20);
    #{$this}__price {
      padding-top: rem(10);
      padding-bottom: rem(10);
      border-bottom: 1px solid  $c-border;
      margin-bottom: rem(20); } } }



.cart-item {
  $this: &;
  @include flex;
  border-bottom: 1px solid  $c-border;
  padding-bottom: rem(10);
  &__img {
    background-repeat: no-repeat;
    background-position: 50% 50%;
    background-size: 70%;
    border-radius: $radius;
    border: 1px solid  $c-border;
    width: rem(130);
    height: rem(130);
    flex: 0 0 rem(130);
    @include xs {
      width: rem(90);
      height: rem(90);
      flex: 0 0 rem(90); } }
  &__descr {
    padding-left: rem(30);
    width: percentage(420/ 1085);
    @include xs {
      padding-left: rem(10); } }
  &__price-wrap {
    width: percentage(400/ 1085);
    @include flex(flex-start, center, row, wrap); }
  &__quantity, &__price {
    width: 50%;
    @include sm {
      width: 100%;
      justify-content: flex-end; } }
  &__delete {
    width: calc(#{percentage((1085 - (420 + 200 + 200)) / 1085)} - #{rem(130)});
    @include xs {
      width: calc(#{percentage((1085 - (420 + 200 + 200)) / 1085)} - #{rem(90)}); } }
  &__title {
    margin-bottom: rem(8);
    a {
      @include tr(color .3s);
      @include hover {
        color: $accent; } } }
  &__subttl {
    color: #a3a3a3; }
  &__label {
    margin-bottom: rem(5);
    font-size: em(10); }
  &__text {
    font-size: em(12); }
  &__delete, &__price, &__quantity {
    display: flex;
    align-items: center; }
  &__price, &__quantity {
    justify-content: center; }
  &__delete {
    justify-content: flex-end; }
  &__quantity-text {
    font-size: em(24);
    font-weight: 500; }

  &--order {
    #{$this}__descr {
      padding-left: rem(10);
      width: percentage(330 / 680);
      @include flex(space-between, normal, column); }
    #{$this}__price-wrap {
      width: calc(#{percentage((680 - 330) / 680)} - #{rem(130)});
      @include xs {
        width: calc(#{percentage((680 - 330) / 680)} - #{rem(90)}); } }
    #{$this}__quantity {}
    #{$this}__price {} } }


.cart-list {
  @include clr;
  font-weight: 500;
  &__title {
    float: left;
    font-size: em(16);
    margin-right: .3em; }
  &__text {
    font-size: em(21);
    position: relative;
    top: -.2em; } }

.cart-controls-blocks {
  @include flex(flex-start, normal, row, wrap);
  &__block {
    width: 50%;
    margin-bottom: rem(20);
    @include xs {
      width: 100%; }
    &--full {
      width: 100%; }
    &--btn {
      @include flex(flex-end, flex-end, column); } }
  &__title {
    text-transform: none;
    font-weight: 500;
    margin-bottom: rem(10); }
  &__input {
    @include notlast {
      margin-bottom: rem(20); } } }
