.items-tabs {
  $this: &;
  &__tabs {
    @include flex;
    margin-bottom: -.3em;
    position: relative;
    z-index: 0; }
  &__tab {
    font-size: em(16);
    padding: .7em 1.5em 1em;
    border-top-right-radius: $radius-lg;
    border-top-left-radius: $radius-lg;
    @include tr(background-color .3s, color .3s);
    @include hover {
      @extend #{$this}__tab--active; }
    @include lg {
      padding: .3em 1em .5em;
      border-top-right-radius: $radius;
      border-top-left-radius: $radius; }
    @include xxs {
      font-size: 1em;
      padding-left: .7em;
      padding-right: .7em; }
    @include r(374) {
      font-size: em(12); }
    &--active {
      background-color: $accent;
      color: $white; } }
  &__content {
    background-color: $white;
    border-radius: $radius;
    @include shadow;
    padding: rem(15);
    position: relative;
    z-index: 2;
    @include notlast {
      margin-bottom: rem(15); }
    @include xs {
      padding: 0;
      box-shadow: none; }
    &--no-shadow {
      box-shadow: none;
      border: 1px solid  $c-border; } }

  &__item {
    @include hidden;
    overflow: hidden;
    max-height: 0;
    @include tr(opacity .3s, visibility .3s);
    &--active {
      @include visible;
      max-height: none;
      overflow: visible; } }
  &__btn {
    text-align: center; } }


