.section {
  padding-top: rem(40);
  padding-bottom: rem(40);
  @include md {
    padding-top: rem(10);
    padding-bottom: rem(10); }
  &__block {
    margin-bottom: rem(30);
    @include xs {
      margin-bottom: rem(15); } }
  &__block-sm {
    margin-bottom: rem(15); }
  &__title {
    margin-bottom: rem(20);
    @include lg {
      text-align: center; } }

  &--bg {
    @include cover;
    background-color: $c-border;
    @include xs {
      background-image: none !important;
      background-color: $white; } } }
